export default {
    data() {
      return {
        formInline: {
          user: '',
          pwd: ''
        }
      }
    },
    methods: {
      onSubmit() {
          if(this.formInline.user=='shuangmaweixh'&&this.formInline.pwd=='Aa88776655'){
              sessionStorage.setItem('isLogin',true)
              this.$router.push('/admin')
          }else{
              this.$message.error('用户或者密码错了哦');
          }
      }
    }
}



