import uploader from '@/components/uploader'
export default {
    components:{
        uploader
    },
    data() {
      return {
        imageUrl: '',
        imgFile:'',
        form: {
            username: '',
            self_ID:'',
            intro:'',
            weibo:'',
            is_member:0
        },
        info:{}
      }
    },
    created(){
        if(this.$route.query.isEdit){
            this.getDetail()
        }
    },
    methods: {
        imgupload(data){
            // console.log(data,'11')
            this.imgFile=data
        },
        getDetail(){
          this.$proxy.get('/api/getUser',{params:{id:this.$route.query.id}})
          .then( (res)=> {
              this.info=res.data.data
              this.form=this.info.people
              this.imageUrl=this.info.people.tx_pic
          })
        },
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
            this.imgFile=file.raw
            console.log(this.imageUrl,file)
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        // if (!isJPG) {
        //   this.$message.error('上传头像图片只能是 JPG 格式!');
        // }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        // return isJPG && isLt2M;
        return isLt2M
      },
      handleEdit(){
          if(this.form.username.trim()==''){
                this.$message.error('请填写小姐姐名字');
                return
            }
            if(this.form.self_ID.trim()==''){
                this.$message.error('请填写小姐姐ID');
                return
            }
            // if(this.form.intro.trim()==''){
            //     this.$message.error('请填写小姐姐介绍');
            //     return
            // }
            if(this.form.weibo.trim()==''){
                this.$message.error('请填写小姐姐微博');
                return
            }
            if(!this.imageUrl){
                this.$message.error('请上传头像');
                return
            }
            let param = new FormData() // 创建form对象
            if(!this.imgFile||!this.imageUrl.includes('blob')){
                //没有编辑，不能提交
                param.delete('tx_pic')
            }
            if(this.imgFile){
                param.append('tx_pic',this.imgFile)
            }
            
            for(let i in this.form){
                if(i=='is_memeber'){
                    param.append(i,Number(this.form[i]))
                    continue
                }
                param.append(i,this.form[i])
            }
            let config = {
                headers: {'Content-Type': 'multipart/form-data'}
            }
            this.$proxy.post('/api/updateUser', param,config)
            .then((res)=> {
                if(res.data.success){
                    this.$message({
                        message: '恭喜你，更新成功',
                        type: 'success'
                    });
                    this.getDetail()
                }else{
                    this.$message.error(res.data.message)
                }
                console.log(res);
            })
      },
      handleAdd(){
          if(this.form.username.trim()==''){
            this.$message.error('请填写小姐姐名字');
            return
        }
        if(this.form.self_ID.trim()==''){
            this.$message.error('请填写小姐姐ID');
            return
        }
        // if(this.form.intro.trim()==''){
        //     this.$message.error('请填写小姐姐介绍');
        //     return
        // }
        if(this.form.weibo.trim()==''){
            this.$message.error('请填写小姐姐微博');
            return
        }
        if(!this.imgFile){
            this.$message.error('请上传头像');
            return
        }
        let param = new FormData() // 创建form对象
        param.append('tx_pic',this.imgFile)
        for(let i in this.form){
            if(i=='is_memeber'){
                param.append(i,Number(this.form[i]))
                continue
            }
            param.append(i,this.form[i])
        }

        let config = {
            headers: {'Content-Type': 'multipart/form-data'}
        }
        this.$proxy.post('/api/insertUser', param,config)
        .then((res)=> {
            if(res.data.success){
                this.$message({
                    message: '恭喜你，添加成功',
                    type: 'success'
                });
            }else{
                this.$message.error(res.data.message)
            }
            console.log(res);
        })
      },
      onSubmit() {
        if(this.$route.query.isEdit==0){
            this.handleAdd()
        }else{
            this.handleEdit()
        }
        console.log('submit!');
      }
    }
}



