export default {
    data(){
        return{
            advList:[]
        }
    },
    methods:{
        getAllAdvs(){
            this.$proxy.get('/api/getAllAdvs').then(res=>{
                this.advList=res.data.data
            })
        },
        handleEit(row,index){
            this.$router.push({path:'/admin/advEdit',query:{isEdit:1,id:row.id}})
            console.log(row)
        },
        handleDelete(row,index){
            this.$confirm('此操作将永久删除该广告, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$proxy.post('/api/deleteAdv',{id:row.id})
                .then((res)=> {
                    if(res.data.success){
                        this.advList.splice(index,1)
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }else{
                        this.$message.error(res.data.message)
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
            console.log(row)
        }
    },
    created(){
        this.getAllAdvs()
    }
}



