export default {
    data(){
        return{
            albumList:[]
        }
    },
    methods:{
        getAllAlbums(){
            this.$proxy.get('/api/getAllAlbums').then(res=>{
                res.data.data.forEach((item)=>{
                    this.albumList.push(item.info)
                })
            })
        },
        handleEit(row){
            this.$router.push({path:'/admin/albumEdit',query:{isEdit:1,id:row.id}})
            console.log(row)
        },
        handleDelete(row,index){
            this.$confirm('此操作将永久删除该写真, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$proxy.post('/api/deleteAlbum',{id:row.id})
                .then((res)=> {
                    if(res.data.success){
                        this.albumList.splice(index,1)
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }else{
                        this.$message.error(res.data.message)
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
            console.log(row)
        }
    },
    created(){
        this.getAllAlbums()
    }
}



