import commonHeader from './header'
export default {
    components:{
        commonHeader
    },
    data(){
      return{
        info:{}
      }
    },
    created(){
      this.getDetail()
    },
    methods:{
        handleClickItem(item){
          // console.log(item)
          this.$router.push(`/detail/${item.info.id}`)
        },
        getDetail(){
          this.$proxy.get('/api/getUser',{params:{id:this.$route.params.id}})
          .then( (res)=> {
              this.info=res.data.data
              // console.log(this.info);
          })
        }
    },
    watch:{
      '$route'(val,oldVal){
        this.getDetail()
      }
    }
}



