import commonHeader from './header'
export default {
    data(){
        return{
            menulist:[{name:'最新',id:1},{name:'本周热门',id:2},{name:'热门推荐',id:3},{name:'双马尾协会成员专区',id:0}],
            menuActive:1,
            peopleList:[],
            advList:[],
            albumLists:[]
          }
    },
    components:{
        commonHeader
    },
    created(){
        this.getAllUsers()
        this.getAllAdvs()
        this.getAllAlbums()
    },
    computed:{
        activeAlbum(){
            let res
            if(this.menuActive==0){
                //双马尾协会成员
                res=this.albumLists.filter((item,index)=>{
                    return item.info.is_member==1
                })
            }else{
                res=this.albumLists.filter((item,index)=>{
                    return item.info.is_member==0
                })
                if(this.menuActive==1){
                    //最新
                    res=this.$R.clone(res)
                    res=res.sort((a,b)=>{
                       return +b.info.creatAt-(+a.info.creatAt)
                    })
                }
                if(this.menuActive==2){
                    //本周热门
                    res=res.filter((item,index)=>{
                        return +this.$moment().startOf('isoWeek')<+item.info.creatAt&&item.info.creatAt<+this.$moment().endOf('isoWeek')
                    })
                    res=res.filter((item,index)=>{
                        return item.info.star>5
                    })
                    res=res.sort((a,b)=>{
                       return b.info.star-a.info.star
                    })
                }
                if(this.menuActive==3){
                    //热门推荐
                    res=res.filter((item,index)=>{
                        return item.info.star>5
                    })
                    res=res.sort((a,b)=>{
                       return b.info.star-a.info.star
                    })
                }
            }
            
            return res
        },
        bannerList(){
            let res=this.albumLists.filter((item,index)=>{
                return item.info.is_banner==1
            })
            return res
        }
    },
    methods:{
        jumpUrl(item){
            window.open(item.jump_url) 
        },
        handleBanner(item){
            // console.log(item)
            this.$router.push(`/detail/${item.info.id}`)
        },
        handleClickStar(item){
            this.$proxy.post('/api/addAlbumStar',{id:item.info.id})
            .then((res)=> {
                if(res.data.success){
                    console.log('点赞成功')
                    item.info.star+=1
                }else{
                    this.$message.error(res.data.message)
                }
            })
            console.log(item)
        },
        changeMunu(item){
            console.log(item)
        },
        jumpInfo(item){
            this.$router.push({name:'Info',params:{id:item.id}})
        },
        getAllUsers(){
            this.$proxy.get('/api/getAllUsers').then(res=>{
                console.log(res)
                this.peopleList=res.data.data.filter((item,index)=>{
                    //展示协会成员
                    return item.is_member==1
                })
            })
        },
        getAllAdvs(){
            this.$proxy.get('/api/getAllAdvs').then(res=>{
                console.log(res)
                this.advList=res.data.data
            })
        },
        getAllAlbums(){
            this.$proxy.get('/api/getAllAlbums').then(res=>{
                console.log(res)
                this.albumLists=res.data.data
            })
        },
        handleClickItem(item){
            console.log(item)
            this.$router.push(`/detail/${item.info.id}`)
        },
        handleClickMenu(item){
            console.log(item)
            this.menuActive=item.id
        },
        handleCarousel(item){

        }
    }
}



