export default {
    data(){
        return{
            active:'people',
            menu:[{name:'小姐姐',id:'people'},{name:'写真集',id:'album'},{name:'广告位',id:'adv'}]
        }
    },
    methods:{
        handleItem(item){
            this.active=item.id
            this.$router.push(`/admin/${item.id}`)
        }
    },
    mounted(){
        //判断是否已经登陆
        if(!sessionStorage.getItem('isLogin')){
            this.$router.push('/admin/login')
        }
    }
}



