
import Search from 'vux/src/components/search/index.vue'

export default {
components:{
VuxSearch:Search,
},
data () {
return {
value: '',
results:[],
list:[],
show:false
}
},
created(){
this.loadAll()
},
methods:{
showInput(){
this.show=true
this.$nextTick(()=>{
this.$refs.search.setFocus()
})
this.results=this.list
},
onCancel(){
this.show=false
},

resultClick(item){
this.$router.push(`/info/${item.other}`)
this.show=false
},
loadAll() {
this.$proxy.get('/api/getAllUsers').then(res=>{
res.data.data.forEach((item)=>{
this.list.push({'title':item.username,'other':item.id})
})
})
},
getResults (val) {
this.results = val ? this.list.filter(item=>{
return item.title.includes(val)
}) : this.list
},
}

}



